import React, { useState, useEffect } from "react";
import { Button } from "./Button";
import { Link } from "react-router-dom";
import "./Navbar.css";

function Navbar() {
	const [click, setClick] = useState(false);
	const [button, setButton] = useState(true);

	const handleClick = () => setClick(!click);
	const closeMobileMenu = () => setClick(false);

	const mailto = () => {
		closeMobileMenu();
		window.location.href = "mailto:caseabo@gmail.com";
	};

	const showButton = () => {
		if (window.innerWidth <= 960) {
			setButton(false);
		} else {
			setButton(true);
		}
	};

	useEffect(() => {
		showButton();
	}, []);

	window.addEventListener("resize", showButton);

	const [colorChange, setColorchange] = useState(false);
	const changeNavbarColor = () => {
		if (window.scrollY >= 160) {
			setColorchange(true);
		} else {
			setColorchange(false);
		}
	};
	window.addEventListener("scroll", changeNavbarColor);

	return (
		<>
			<nav className={colorChange ? "navbar colorChange" : "navbar"}>
				<div className="navbar-container">
					<Link to="/" className="navbar-logo" onClick={closeMobileMenu}>
						CASE À BO
					</Link>
					<div className="menu-icon" onClick={handleClick}>
						<i className={click ? "fas fa-times" : "fas fa-bars"} />
					</div>
					<ul className={click ? "nav-menu active" : "nav-menu"}>
						<li className="nav-item">
							<Link to="/" className="nav-links" onClick={closeMobileMenu}>
								ACCUEIL
							</Link>
						</li>
						<li className="nav-item">
							<Link to="/villa" className="nav-links" onClick={closeMobileMenu}>
								VILLA
							</Link>
						</li>
						<li className="nav-item">
							<Link to="/martinique" className="nav-links" onClick={closeMobileMenu}>
								MARTINIQUE
							</Link>
						</li>
						{/* <li className="nav-item">
							<Link to="/infos" className="nav-links" onClick={closeMobileMenu}>
								INFOS
							</Link>
						</li> */}

						<li>
							<Link to="/" className="nav-links-mobile" onClick={mailto}>
								CONTACT
							</Link>
						</li>
					</ul>
					{button && (
						<Button
							buttonStyle="btn--outline"
							onClick={() => (window.location.href = "mailto:caseabo@gmail.com")}
						>
							CONTACT
						</Button>
					)}
				</div>
			</nav>
		</>
	);
}

export default Navbar;
